html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5E311E;
}

.bg-blanco {
  background-color: #FFFFFF;
}

.mt-5 {
  margin-top: 5rem;
}

.margen-menos-10 {
  margin-top: -10rem;
}

.pad-bot-0 {
  padding-bottom: 0 !important;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.h2-sombra1 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra1 h2::after {
  content: "";
  background-color: #5E311E;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.h2-sombra2 h2 {
  font-size: 3.6rem;
  display: inline-block;
}
.h2-sombra2 h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: 105%;
  display: block;
  margin: -1.4rem 0 0 0;
}

.degradado {
  color: transparent;
  background: linear-gradient(to right, #5E311E 0%, #C39C63 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

.btn-basico {
  color: #343a40;
  background-color: #FFFFFF;
  border: 1px solid #343a40;
  padding: 0.5rem 2rem;
  transition: background-color 0.3s;
}
.btn-basico:hover {
  background-color: #343a40;
  color: #FFFFFF;
}

h1, h2, h3 {
  font-weight: 900;
  margin: 1rem 0 4rem 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 5.8rem;
}

h3 {
  font-size: 5rem;
}

p {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

/* Añade a este archivo los demas archivos de esta carpeta con @forward y recuerda añadadirlos al archivo principal (app.scss) */
.header {
  min-height: 60rem;
  background-image: url(/build/img/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  padding: 5rem 0;
}
.header .header-contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 56rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header .header-barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .nombre-sitio {
  text-align: center;
  font-weight: 300;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .header .nombre-sitio {
    text-align: left;
    font-size: 5rem;
  }
}
.header .nombre-sitio span {
  font-weight: 900;
}
.header .contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header .contacto {
    align-items: flex-end;
  }
}
.header .telefono {
  display: flex;
  justify-content: center;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
}
.header .telefono::before {
  content: "";
  background-image: url(/build/img/telefono.png);
  background-repeat: no-repeat;
  background-position: center center;
  display: block;
  height: 4rem;
  width: 4rem;
  margin-right: 1rem;
}
.header .navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header .navegacion {
    flex-direction: row;
    margin-top: 0;
  }
}
.header .navegacion .link {
  color: #FFFFFF;
  font-size: 2rem;
  padding: 1rem 2rem;
  width: 100%;
  text-align: center;
  transition: background-color 0.3s;
}
.header .navegacion .link:hover {
  background-color: rgba(255, 255, 255, 0.5);
  color: #000000;
}
.header .header-slogan {
  text-align: center;
}
.header .header-slogan h1 {
  font-size: 3rem;
  margin: 0;
}
.header .header-slogan p {
  font-size: 2rem;
  margin: 0;
  line-height: 1;
}
@media (min-width: 768px) {
  .header .header-slogan {
    text-align: right;
  }
  .header .header-slogan h1 {
    font-size: 5rem;
  }
  .header .header-slogan p {
    font-size: 2.5rem;
    margin: 0;
    line-height: 1;
  }
}

.nosotros {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  background-image: url(/build/img/nosotros.jpg);
  background-repeat: no-repeat;
  background-position: bottom center;
}
@media (min-width: 768px) {
  .nosotros {
    grid-template-columns: repeat(3, 1fr);
  }
}
.nosotros .nosotros-contenido {
  background-color: #FFFFFF;
  padding: 5rem;
}
.nosotros .nosotros-contenido h2, .nosotros .nosotros-contenido p {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nosotros .nosotros-contenido {
    grid-column: 2/4;
  }
}

.modelo {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  background-repeat: no-repeat;
  background-position: bottom;
}
@media (min-width: 768px) {
  .modelo {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 5rem;
  }
  .modelo .modelo-contenido {
    padding: 5rem;
  }
}
.modelo .modelo-contenido {
  background-color: #FFFFFF;
  padding: 2rem;
}

.basico {
  background-image: url(/build/img/modelo_basico.jpg);
}
@media (min-width: 768px) {
  .basico {
    background-position: 100% 25px;
  }
  .basico .modelo-contenido {
    grid-row: 1/2;
    padding: 2rem 2rem 2rem 0;
  }
}

.premier {
  background-image: url(/build/img/modelo_premier.jpg);
}
@media (min-width: 768px) {
  .premier {
    background-position: 0 0;
  }
  .premier .modelo-contenido {
    grid-column: 2/3;
    padding: 0 2rem 2rem 2rem;
  }
}

.elite {
  background-image: url(/build/img/modelo_elite.jpg);
}
@media (min-width: 768px) {
  .elite {
    background-position: top right;
    margin-top: 0;
  }
  .elite .modelo-contenido {
    padding: 0 2rem 2rem 0;
  }
}

.galeria {
  text-align: center;
}
.galeria .galeria-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .galeria .galeria-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.galeria .galeria-imagen img {
  object-fit: cover;
  height: 100%;
}
.galeria .galeria-imagen:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/3;
}
.galeria .galeria-imagen:nth-child(3) {
  grid-column: 2/3;
  grid-row: 3/5;
}
.galeria .galeria-imagen:nth-child(6) {
  grid-column: 1/2;
  grid-row: 2/4;
}
@media (min-width: 768px) {
  .galeria .galeria-imagen:nth-child(2) {
    grid-column: 1/2;
    grid-row: 2/4;
  }
  .galeria .galeria-imagen:nth-child(6) {
    grid-column: 3/4;
    grid-row: 1/3;
  }
  .galeria .galeria-imagen:nth-child(3) {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}

.footer {
  min-height: 60rem;
  background-image: url(/build/img/footer_bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
@media (min-width: 768px) {
  .footer {
    min-height: 30rem;
  }
}
@media (min-width: 768px) {
  .footer .footer-barra {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.footer .nombre-sitio {
  color: #FFFFFF;
  text-align: center;
  font-weight: 300;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .footer .nombre-sitio {
    text-align: left;
    font-size: 5rem;
  }
}
.footer .nombre-sitio span {
  font-weight: 900;
}
.footer .contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contacto {
    align-items: flex-end;
  }
}
.footer .telefono {
  display: flex;
  justify-content: center;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
}
.footer .telefono::before {
  content: "";
  background-image: url(/build/img/telefono.png);
  background-repeat: no-repeat;
  background-position: center center;
  display: block;
  height: 4rem;
  width: 4rem;
  margin-right: 1rem;
}
.footer .navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .footer .navegacion {
    flex-direction: row;
    margin-top: 0;
  }
}
.footer .navegacion .link {
  color: #FFFFFF;
  font-size: 2rem;
  padding: 1rem 2rem;
  width: 100%;
  text-align: center;
  transition: background-color 0.3s;
}
.footer .navegacion .link:hover {
  background-color: rgba(255, 255, 255, 0.5);
  color: #000000;
}
.footer p {
  padding: 1.5rem 0;
  background-color: #343a40;
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}

